	background: rgb(247,95,93);
background: linear-gradient(45deg, rgba(247,95,93,1) 0%, rgba(126,27,120,1) 32%, rgba(71,189,217,1) 100%);
} */

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  text-align: center;
  
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body{font-family:'Lato', sans-serif}form{display:flex;justify-content:center;width:70%;margin:0 auto;border:10px solid #AE7CA2}form input:first-child{width:100%;height:50px;padding:0 15px;font-size:1.5rem;background:#000;color:#fff;outline:none}form button{font-size:1.5rem;background:#AE7CA2;border:0}form button:focus{outline:none}form button i{cursor:pointer;transition:1s all}form button i:hover{transform:scale(1.2)}.btn{float:right}#change-name{background:rgba(176,179,210,0.644);border:0px;border-radius:50px;padding:15px 25px;color:#fff;font-size:1rem;cursor:pointer}#change-name:focus{border:0px;outline:none}#change-name:hover{background:rgba(176,179,210,0.408)}.name{display:flex;justify-content:space-between;width:70%;margin:20px auto}.name .btn{transition:1s all}.name .btn:hover{transform:scale(1.2)}.name h1{margin:0;font-family:'Raleway', sans-serif;font-size:3rem;color:#fff}#m{border:10px solid #259cac;display:flex;flex-direction:column;justify-content:right;min-height:400px;width:70%;height:400px;margin:0 auto;overflow-y:scroll;overflow-x:hidden;background:#000}#m .ms{padding:15px}#m p{margin:15px 10px 0 0;color:#fff;text-align:right}#m p #msg{background:#442ab4;display:inline-block;padding:10px 25px;border-radius:50px;color:#fff;max-width:90%;word-wrap:break-word}#m p #time{color:#a3a3a3;font-size:.6rem}.hidden{visibility:hidden}

.modal_content{position:fixed;top:-5px;left:0px;width:100%;height:105%;z-index:77;background:rgba(0,0,0,0.887);color:#fff;font-size:1.5rem}.container{margin:10% auto;height:100%;width:40%}.container div{display:flex}.container div span{text-align:right;display:block;cursor:pointer;transition:1s all;font-size:2rem}.container div span:first-child{width:90%}.container div span:last-child:hover{transform:rotate(360deg) scale(2);color:red}

